.Form {
    display: -ms-flexbox;
    display: flex;
    padding: 6rem 4rem;
}

.form {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;

}

.input {
    width: 80%;
    border: none;
    border-radius: 5px;
    -webkit-box-shadow: 2px 2px 4px #000;
            box-shadow: 2px 2px 4px #000;
    padding: .7rem .5rem;
    margin: 1.3rem 0;
}

.form__preview {
width: 50%;
display: -ms-flexbox;
display: flex;
-ms-flex-align: center;
    align-items: center;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-direction: column;
    flex-direction: column;
}

.add__btn {
    padding: .8rem 1rem;
    border: none;
    border-radius: 4px;
    background-color: orange;
    color: #fff;
}

.image__container {
    width: 20rem;
    height: 15rem;
}

.image__container img {
    width: 100%;
    height: 100%;
}


